*,
*:after,
*:before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

body,
html {
  height: 100%; }

body {
  background: #fff;
  color: #000;
  font-family: "Lato", Arial, sans-serif;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased; }

.wrapper {
  min-height: 100%;
  height: auto !important;
  height: 100%;
  margin: 0 auto -60px; }

.push {
  height: 60px; }

.carousel {
  position: relative;
  overflow: hidden;
  max-width: 1000px;
  margin: 0 auto; }
  .carousel:hover .btn-next,
  .carousel:hover .btn-prev {
    display: block; }
  .carousel ul {
    position: relative;
    margin: 0;
    padding: 0;
    list-style: none;
    max-height: 652px;
    overflow: hidden; }
    .carousel ul li {
      float: left;
      margin: 0;
      padding: 0;
      position: relative;
      width: 100%;
      text-align: center; }
      .carousel ul li .slide-text {
        position: absolute;
        left: 50px;
        bottom: 50px;
        text-align: left;
        color: #fff; }

.slide {
  display: none; }

.active {
  display: block; }

.carousel a {
  position: absolute;
  display: block;
  height: 100%;
  width: 75px;
  z-index: 1; }

.carousel a:before {
  content: '';
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: solid #fff;
  border-width: 7px 0 0;
  display: inline-block;
  height: 27px;
  width: 27px;
  position: absolute;
  top: calc(50% - 50px); }

.carousel a:hover:before {
  border-color: #fff; }

.btn-prev {
  left: 0; }

.btn-prev:before {
  -webkit-transform: rotate(-45deg);
  border-left-width: 7px !important;
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
  margin-left: 35px; }

.btn-next {
  right: 0; }

.btn-next:before {
  -webkit-transform: rotate(45deg);
  border-right-width: 7px !important;
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
  margin-left: 10px; }

.indicator {
  height: 20px;
  z-index: 9999;
  text-align: center;
  position: relative;
  bottom: 50px;
  width: 130px;
  margin: 0 auto; }

.indicator ol {
  padding: 0;
  list-style: none; }

.dot {
  display: inline-block;
  border: 2px solid rgba(255, 255, 255, 0.8);
  border-radius: 50%;
  width: 20px;
  height: 20px; }

.dot-active {
  background: rgba(255, 255, 255, 0.8);
  border: none; }

#slide-1 {
  background: url("../img/slide-1-lg.jpg") no-repeat;
  background-size: contain; }

#slide-2 {
  background: url("../img/slide-2-lg.jpg") no-repeat;
  background-size: contain; }

#slide-3 {
  background: url("../img/slide-3-lg.jpg") no-repeat;
  background-size: contain; }

#slide-4 {
  background: url("../img/slide-4-lg.jpg") no-repeat;
  background-size: contain; }

@media (max-width: 500px) {
  #slide-1 {
    background: url("../img/slide-1-sm.jpg") no-repeat;
    background-size: contain; }
  #slide-2 {
    background: url("../img/slide-2-sm.jpg") no-repeat;
    background-size: contain; }
  #slide-3 {
    background: url("../img/slide-3-sm.jpg") no-repeat;
    background-size: contain; }
  #slide-4 {
    background: url("../img/slide-4-sm.jpg") no-repeat;
    background-size: contain; }
  .carousel a:before {
    content: '';
    border-width: 5px 0 0;
    height: 20px;
    width: 20px; }
  .btn-prev:before {
    -webkit-transform: rotate(-45deg);
    border-left-width: 5px !important;
    -ms-transform: rotate(-45deg);
    transform: rotate(-45deg);
    margin-left: 10px; }
  .btn-next:before {
    -webkit-transform: rotate(45deg);
    border-right-width: 5px !important;
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
    margin-left: 35px; }
  .indicator {
    bottom: 45px;
    margin: 0 auto; }
  .dot {
    width: 12px;
    height: 12px; }
  .carousel ul li .slide-text {
    left: 20px;
    bottom: 40px; } }

footer {
  background: rgba(0, 0, 0, 0.2);
  height: 60px;
  padding: 10px 0; }

.footer-list {
  display: inline-block;
  list-style-type: none;
  margin: 0;
  padding: 0 0 0 15px; }
  .footer-list li {
    display: inline-block;
    margin: 10px;
    color: #000; }
  .footer-list a {
    color: #000;
    font-size: 13px;
    cursor: pointer; }
  .footer-list a:active,
  .footer-list a:focus,
  .footer-list a:hover,
  .footer-list a:visited {
    color: #000; }

.footer-links {
  color: #fff;
  display: block;
  margin: 0 auto;
  text-align: center;
  vertical-align: middle;
  width: 100%; }

.slide-text h3 {
  font-size: 42px;
  font-weight: 700;
  margin: 0 auto; }

.slide-text span {
  font-size: 18px; }

@media (max-width: 420px) {
  .slide-text h3 {
    font-size: 28px; }
  .slide-text span {
    font-size: 14px; } }
